.mf-info {
  height: 100%;
  flex: 1 1;
  padding: 40px 0px;
}
.mf-info .mf-info-title {
  font-size: 20px;
  color: #333333;
  letter-spacing: 0.19px;
  padding-left: 40px;
}
.mf-info .mf-info-content {
  padding-top: 30px;
}
.mf-info .mf-info-content .mf-info-content__prompt {
  display: inline-block;
  width: 400px;
  vertical-align: middle;
  margin-left: 16px;
  padding: auto;
}
.mf-info .mf-info-content .mf-info-content__prompt span {
  line-height: 24px;
  word-break: normal;
  /* width: auto; */
  display: block;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden;
}
.mf-info .ant-input {
  width: 384px;
}
.mf-info form .ant-select,
.mf-info form .ant-cascader-picker {
  width: 384px;
}
.mf-info .ant-form-item-control-wrapper {
  margin-left: 20px;
}
